import React, {useEffect, useState} from 'react'
import { Link } from 'react-router-dom'
import {useSelector, useDispatch} from 'react-redux'
import { increaseQuantityCart, decreaseQuantityCart, deleteProduct, showResultPrice } from '../../redux/handleSlice'
import '../../scss/index.scss'  

const CartProductPage = () => {
  const products = useSelector(state => state.product.checkoutProductList)
  // const [productList, setProductList] = useState(() => {
  //   const storageProduct = JSON.parse(localStorage.getItem('products'))
  //   return storageProduct ?? products
  // })

  // useEffect(() => {
  //   localStorage.setItem('products', JSON.stringify(products))
  // }, [products])

  const dispatch = useDispatch()
  const increaseProduct = (product) => {
    dispatch(increaseQuantityCart(product))
  }

  const decreaseProduct = (product) => {
    dispatch(decreaseQuantityCart(product))
  } 

  const removeProduct = (product) => {
    dispatch(deleteProduct(product))
  }

  const resultPrice = products.reduce((price, item) => price + item.totalPrice , 0) 

  return (
      <div className="content">
      {
        products ? (
          <div className="cart-wrap"> 
          <div className='cart-wrap-top'>
            <div className='cart-wrap-title'>
              <Link className='link' to='/'><span>Trang ch</span></Link>
              <span>Gi hng</span>
            </div>

            <div className="cart-page">
              <div className="cart-page-left">
                <div className='title'>Gi hng</div>
                <table>
                  <tr>
                    <th className='product'>Sn phm</th>
                    <th className='quantity'>S lng</th>
                    <th className='unit-price'>n gi</th>
                    <th className='price'>Thnh tin</th>
                  </tr>
                    {
                      products && products.map(product => {
                        return (
                          <tr key={product.id}>
                            <th className='product'>
                              <div className='product-wrap'>
                                <img src={product.img}/>
                                <div className='product-wrap-des'>
                                  <span>{product.name}</span>
                                  <div className='cart-wrap-delete' onClick={() => removeProduct(product)}>
                                    <i class="fa-solid fa-cart-arrow-down"></i>
                                    <span>Xa</span>
                                  </div>
                                </div>
                              </div>
                            </th>
                            <th>
                              <div className='product-detail-quantity'>
                                  <span><i class="fa-solid fa-minus" onClick={() => decreaseProduct(product)} ></i></span>
                                  <span>{product.quantity}</span>
                                  <span ><i class="fa-solid fa-plus" onClick={() => increaseProduct(product)} ></i></span>
                              </div>
                            </th>
                            <th className='th-price'>{product.price} VN</th>
                            <th >{product.totalPrice}.000.000VN</th>
                          </tr>
                        )
                      })
                    }
                  <tr>
                    <td className='total-price' colSpan="4">Tng tin {resultPrice}.000.000 VN</td>
                  </tr>
                  <tr>
                    <td className='btn-wrap' colSpan="4">
                      <Link to='/'><button>Tip tc mua hng</button></Link>
                      <Link to='/checkout' ><button>Thc hin thanh ton</button></Link>
                    </td>
                  </tr>
                </table> 
              </div>

              <div className="cart-page-right">
                <div className="cart-page-relation">
                  <p>Bi vit mi</p>
                  <ul>
                     <Link className='link' to='/productpage3'><li>B quyt trang tr bn tr  tng v p, sang trng cho phng khch</li></Link>
                    <Link className='link' to='/productpage4'><li>Nhng mu thit k ni tht phng khch hin i nht nm 2018</li></Link>
                    <Link className='link' to='/productpage'><li>Thit k v thi cng ni tht chung c hin i (Anh H - Tn Mai)</li></Link>
                    <Link className='link' to='/productpage2'><li>Thit k ni tht chung c hin i The SunShine Place</li></Link>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
        ) : 
        (<h1>Khng c sn phm no trong gi hng. Quay li <Link className='link' to='/'><span>Ca Hng</span></Link>  tip tc mua sm.</h1>)
      }
      </div>
  )
}

export default CartProductPage

