import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { Link } from 'react-router-dom'
import '../../scss/index.scss'

const NewsPage = () => {

  const [newsProducts, setNewsProducts] = useState([])

  useEffect(() => {
    const fetchData = async () => {
        const res = await axios.get("http://localhost:3001/products")
        const data = res.data

        const newProducts = data.filter(x => x.type == "tintuc")
        setNewsProducts(newProducts)
    }
    fetchData()
  }, [])

  return (
    <div className="content">
      <div className="products-wrap"> 
        <div className='products-wrap-top'>
          <div className='products-wrap-title'>
              <Link className='link' to='/'><span>Trang ch</span></Link>
              <span>D n tiu biu</span>
          </div>

          <div className="news-page">
            <div className="news-page-left">
              {
                newsProducts.map((newsProduct, item) => {
                  return (
                    <div className='news-page-wrap' key={item}>
                      <p className='news-page-order'>{newsProduct.order}:</p>
                      <img className='news-page-img' src={newsProduct.img} alt="" />
                      <p className='news-page-title'>{newsProduct.title}</p>
                      <p className='news-page-des'>Tn d n: {newsProduct.des}</p>
                      
                    </div>
                  )
                })
              }
            </div>
            <div className="news-page-right">
              <div className="news-page-relation">
                <p>Bi vit mi</p>
                <ul>
                  <Link className='link' to='/productpage3'><li>B quyt trang tr bn tr  tng v p, sang trng cho phng khch</li></Link>
                  <Link className='link' to='/productpage4'><li>Nhng mu thit k ni tht phng khch hin i nht nm 2018</li></Link>
                  <Link className='link' to='/productpage'><li>Thit k v thi cng ni tht chung c hin i (Anh H - Tn Mai)</li></Link>
                  <Link className='link' to='/productpage2'><li>Thit k ni tht chung c hin i The SunShine Place</li></Link>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default NewsPage