import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { Link } from 'react-router-dom'
import '../../scss/index.scss'

const ProjectPage = () => {
  const [projectProducts, setProjectProducts] = useState([])

  useEffect(() => {
    const fetchData = async () => {
        const res = await axios.get("http://localhost:3001/products")
        const data = res.data

        const newProducts = data.filter(x => x.type == "duantieubieu")
        setProjectProducts(newProducts)
    }
    fetchData()
  }, [])


  return (
    <div className="content">
      <div className="products-wrap"> 
        <div className='products-wrap-top'>
          <div className='products-wrap-title'>
              <Link className='link' to='/'><span>Trang ch</span></Link>
              <span>D n tiu biu</span>
          </div>

          <div className="project-page">
            <div className="project-page-left">
                {
                  projectProducts.map((projectProduct, item) => {
                    return (
                      <div className='project-page-wrap' key={item}>
                        <p className='project-page-order'>{projectProduct.order}:</p>
                        <img className='project-page-img' src={projectProduct.img} alt="" />
                        <p className='project-page-title'>{projectProduct.title}</p>
                        <p className='project-page-name'>Tn d n: {projectProduct.name}</p>
                        <p className='project-page-code'>M: {projectProduct.code}</p>
                        <p className='project-page-investor'>Ch u t: {projectProduct.investor}</p>
                        <p className='project-page-time'>Thi gian thi cng: {projectProduct.time}</p>
                        <p className='project-page-place'>V tr: {projectProduct.place}</p>
                      </div>
                    )
                  })
                }
            </div>
            <div className="project-page-right">
                <div className="project-page-relation">
                  <p>Bi vit mi</p>
                  <ul>
                    <Link className='link' to='/productpage3'><li>B quyt trang tr bn tr  tng v p, sang trng cho phng khch</li></Link>
                    <Link className='link' to='/productpage4'><li>Nhng mu thit k ni tht phng khch hin i nht nm 2018</li></Link>
                    <Link className='link' to='/productpage'><li>Thit k v thi cng ni tht chung c hin i (Anh H - Tn Mai)</li></Link>
                    <Link className='link' to='/productpage2'><li>Thit k ni tht chung c hin i The SunShine Place</li></Link>
                  </ul>
                </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default ProjectPage