import React, {useRef} from 'react'
import emailjs from 'emailjs-com'

const Mail = () => {

  const form = useRef()

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('service_2fro65i', 'template_ps8flwd', form.current, 'H8JqjdFv2aPV9tTP_')
    e.target.reset()
  };

  return (
    <div className='contact-form'>
      <div className="contact-form-text"> li thng tin  c t vn</div>
      <form ref={form} onSubmit={sendEmail} action="">
        <input type="text" name='name' placeholder='Tn ca qu khch hng' />
        <input type="email" name='email' placeholder='a ch Email' />
        <input type="phone" name='phone' placeholder='S in thoi lin h' />
        <input type="text" placeholder='a ch' />
        <textarea name="text" id="" cols="30" rows="3" placeholder='M t'></textarea>
        <button type='submit'>ng k ngay</button>
      </form>
    </div>
  )
}

export default Mail