import React from 'react'
import { useDispatch ,useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import { deleteProduct } from '../../redux/handleSlice'
import '../../scss/index.scss'

const CheckoutPage = () => {

  const products = useSelector(state => state.product.checkoutProductList)

  const resultPrice = products.reduce((price, item) => price + item.totalPrice , 0) 

  const dispatch = useDispatch()
  const removeProduct = (product) => {
    dispatch(deleteProduct(product))
  }

  return (
    <div className='checkout'>
        <div className='checkout-wrap'>
            <div className="checkout-left">
              <div className='checkout-product'>
                <div className='title'>Gi hng (2)</div>
                <div className='product-wrap'>
                {
                  products.map(product => {
                    return (
                      <div className='product'key={product.id}>
                        <div className='product-img'> 
                          <img src={product.img}/>
                        </div>
                        <div className='product-info'>
                          <h3>{product.name}</h3>
                          <p>M sn phm: <span>{product.code}</span></p>
                          <p>Tnh trng: <span>Cn hng</span></p>
                          <p>Gi thnh: {product.price}</p>
                          <p>S lng: {product.quantity}</p>
                          <div className="product-btn-small" onClick={() => removeProduct(product)}>
                            <button>Xa</button>
                          </div>
                        </div>
                        <div className="product-btn" onClick={() => removeProduct(product)}>
                          <button>Xa</button>
                        </div>
                      </div>
                    )
                  }) 
                }
                </div>
              </div>
              <Link className='link' to='/'><button className='checkout-btn'>Tip tc mua hng</button></Link>
              <div className='checkout-info'>
                <div className='title'>Thng tin khch hng</div>
                <div className='info'>
                  <div className="info-wrap">
                    <form action="">
                      <div className='input-wrap'>
                        <label>H v tn</label>
                        <input type="text" placeholder='H v Tn'/>
                      </div>
                      <div className='input-wrap'>
                        <label>Email</label>
                        <input type="email" placeholder='Email'/>
                      </div>
                      <div className='input-wrap'>
                        <label>S in thoi</label>
                        <input type="number" placeholder='S in thoi'/>
                      </div>
                      <div className='input-wrap'>
                        <label>a ch</label>
                        <input type="text" placeholder='a ch'/>
                      </div>
                      <div className='input-wrap'>
                        <label>Tnh / Thnh</label>
                        <select name="" id="">
                          <option value="">Chn tnh thnh</option>
                      </select>
                      </div>
                      <div className='input-wrap'>
                        <label>Qun / Huyn</label>
                        <select name="" id="">
                          <option value="">Chn qun huyn</option>
                      </select>
                      </div>
                      <div className='input-wrap'>
                        <label>Phng / X</label>
                        <select name="" id="">
                          <option value="">Chn phng x</option>
                      </select>
                      </div>
                      
                    </form>
                    <div className="payment">
                      <h3>Phng thc thanh ton</h3>
                      <div className='input-method-wrap'>
                          <input type="radio" name='check'/>
                          <label>Thanh ton ti nh (Quy Khach vui long thanh toan hoa n khi nhn c hang. Cam n Quy Khach!)</label>
                      </div>
                      <div className='input-method-wrap'>
                          <input type="radio" name='check'/>
                          <label>Bng v in t Bo Kim (ng nhp  thc hin chc nng ny)</label>
                      </div>
                      <div className='input-method-wrap'>
                        <textarea name="" id="" placeholder='Ghi ch'></textarea>
                      </div>  
                    </div>
                  </div>

                </div>
              </div>
            </div>

            <div className="checkout-right">
                <div className='checkout-bill'>
                  <h1>THANH TON</h1>
                  <div className='discount-title'>NHP M U I ( M COUPON, VOUCHER )</div>
                  <p>M gim gi</p>
                  <div className='discount-wrap'>
                    <input type="text" placeholder='M gim gi'/>
                    <button>S dng</button>
                  </div>
                  <div className='price-temporary'>
                    <span>TM TNH</span>
                    <span>{resultPrice && resultPrice}.000.000VN</span>
                  </div>
                  <div className='fees-ship'>
                    <span>PH VN CHUYN</span>
                    <span>0</span>
                  </div>
                  <div className='price'>
                    <span>THNH TIN</span>
                    <span>{resultPrice && resultPrice}.000.000VN</span>
                  </div>
                  <button>Thanh TON</button>
                </div>
            </div>
        </div>
    </div>
  )
}

export default CheckoutPage